    import {  
        mapState,  
        mapMutations  
    } from 'vuex';
		
	export default {
		data() {
			return {
				// login: this.$store.state.hasLogin,
				avatarUrl: "/static/img/visitor.png",
				// uerInfo: mapState(['avatarUrl', 'userName'])				
			}
		},
		computed:mapState({
			login: 'hasLogin',			
			uerInfo: state => {
				console.log('tabbar5-avatarUrl', state);
				return {
				avatarUrl: state.avatarUrl,
				userName: state.userName,	
			}}
		}),
		methods: {
			...mapMutations(['logout']),
			goLogin() {
				if (!this.login) {
					console.log("点击前往登录")
					uni.navigateTo({
						url: '/pages/login/login',
					})
				}
			},
			bindLogout() {
				this.logout();
			}
		}
	}
